function computeSankey(sankeyWidth,height){
    const sankey = d3.sankey()
      .nodeId(d => d.name)
      .nodeAlign(d3.sankeyLeft)
      .nodeWidth(100)
      .nodePadding(10)
      .extent([[1, 5], [sankeyWidth - 1, height - 5]]);

    return ({nodes, links}) => sankey({
        nodes: nodes.map(d => Object.assign({}, d)),
        links: links.map(d => Object.assign({}, d))
    });
}

function formatData(data){
    // console.log(data)

    const links = data.reduce(function (allLinks, element){
        const link = {'source': element.source, 'target': element.target, 'value': element.value}
        if (!(element.diagram in allLinks)) {
            allLinks[element.diagram] = {'nodes':[], 'links':[]}
        }
        
        
        allLinks[element.diagram].links.push(link)
        return allLinks
    }, {})

    Object.keys(links).forEach(function(key) {
        if(key != 'Principal'){
            links.Principal.links.forEach(function(item){
                if(item.source != 'Interactions'){
                    links[key].links.push(item)
                }
            });

        }
        links[key].nodes = Array.from(new Set(links[key].links.flatMap(l => [l.source, l.target])), name => ({name, category: name.replace(/ .*/, "")}))
    })


    console.log(links)
    // const nodes = Array.from(new Set(links.Principal.flatMap(l => [l.source, l.target])), name => ({name, category: name.replace(/ .*/, "")}));
    
    return links//{'node': nodes, 'links' : links}
}

function drawSelectedSankey(data, selectedNode, color, width, height) {
    if(data[selectedNode]){
        drawNodes(data, data[selectedNode].nodes,color, width, height)
        drawLinks(data[selectedNode].links, color)
        addLabels(data[selectedNode].nodes, width, height)

        d3.selectAll('.link')
        .style("stroke", d => ((selectedNode != d.source.name) && (d.source.name != 'Interactions')) ? '#aaa' : color(selectedNode))
        .attr("stroke-opacity", d => ((selectedNode != d.source.name) && (d.source.name != 'Interactions')) ? '0.5' : '1')
    } else {
        drawNodes(data, data['Principal'].nodes,color, width, height)
        drawLinks(data['Principal'].links,color)
        addLabels(data['Principal'].nodes, width, height)
    }
}

function drawNodes(data, nodes, color, width, height){
    d3.select(".viz1-svg").append("g")
                .attr("stroke", "#000")
                .selectAll("rect")
                .data(nodes)
                .join("rect")
                .attr("class", "node")
                .attr("x", d => d.x0)
                .attr("y", d => d.y0)
                .attr("height", d => d.y1 - d.y0)
                .attr("width", d => d.x1 - d.x0)
                .attr("rx", 5).attr("ry", 5)
                .attr("fill", d => color(d.category === undefined ? d.name : d.category))
                .on("click", (d) => { focusLink(data, d.name, color, width, height) })
                .append("title")
                
    
}
function focusLink(data, selectedNode, color, width, height){
    console.log("CLICL CLICL", data)
    d3.selectAll('.node').remove()
    d3.selectAll('.link').remove()
    d3.selectAll('.sankeyLabel').remove()
    d3.select(".viz1-svg").selectAll('text').remove()
    drawSelectedSankey(data, selectedNode, color, )
    // drawLinks(data[selectedNode.name].links,color)
    
}

function addLabels(nodes, width, height){
    d3.select(".viz1-svg").append("g")
            .attr("class", "sankey-label")//.attr("font-family", "sans-serif").attr("font-size", 10)
            .selectAll("text")
            .data(nodes)
            .join("text")
            .attr("x", d => d.x0 + (d.x1- d.x0)/2)
            .attr("y", d => (d.y1 + d.y0) / 2)
            .attr("dy", "0.35em")
            .attr("text-anchor", 'middle')//d => d.x0 < width / 2 ? "start" : "end")
            .text(d => d.name);
    
    addIcons(nodes,width,height)
}


function addIcons(nodes, width, height){
    const react = ["likes", "Love", "Haha", "Wow", "Triste", "Colere", "Solid"]
    d3.select(".viz1-svg").append("g")
            .attr("class", "sankey-label")//.attr("font-family", "sans-serif").attr("font-size", 10)
            .selectAll("text")
            .data(nodes)
            .join("image")
            .attr("x", d => d.x0 - 6)
            .attr("y", d => (d.y1 + d.y0) / 2)
            .attr("xlink:href", d => react.includes(d.name)?"./"+d.name+".png":"");

//             ("svg:image")
// .attr('x', -9)
// .attr('y', -12)
// .attr('width', 20)
// .attr('height', 24)
// .attr("xlink:href", "resources/images/check.png")
}

function drawLinks(links,color){
    const link = d3.select(".viz1-svg").append("g")
                .attr("fill", "none")
                .attr("stroke-opacity", 0.5)
                .selectAll("g")
                .data(links)
                .join("g")
                .style("mix-blend-mode", "multiply");
      
      
    link.append("path")
        .attr("class", "link")
        .attr("d", d3.sankeyLinkHorizontal())
        .attr("stroke", d => color(d.source.category === undefined ? d.source.name : d.source.category))//"#aaa")
        .attr("stroke-width", d => Math.max(1, d.width));
      
    link.append("title")
        .text(d => `${d.source.name} → ${d.target.name}`);
}


